import React from 'react';

import { useIntervalValue } from './useIntervalValue';

const global = window;

export type UseLocationQsProps = {
  interval?: number;
  window?: Window;
};

export function useLocationSearchParams({
  interval = 250,
  window = global,
}: UseLocationQsProps = {}): URLSearchParams {
  const search = useIntervalValue(
    () => window.location.search,
    window.location.search,
    { interval },
  );
  const [params, setParams] = React.useState(() => new URLSearchParams(search));

  React.useEffect(() => setParams(new URLSearchParams(search)), [search]);
  return params;
}
