/* eslint-disable no-console */
import Ajv from 'ajv';
import deepExtend from 'deep-extend';
import React from 'react';
import { v4 as uuidv4 } from 'uuid';

import { EventContext } from '../../../api/src/domain/EventContext';
import {
  EnactiveMetadata,
  EnactiveMetadataSchema,
} from '../domain/EnactiveMetadata';
import { RecursivePartial } from '../util/RecursivePartial';
import { attributesToObject } from '../util/attrsToObject';
import { nullishAssignment } from '../util/nullishAssignment';
import { useLocationHref } from './useLocationHref';
import { useLocationSearchParams } from './useLocationQs';

const compiled = new Ajv({ coerceTypes: true }).compile<EnactiveMetadata>(
  EnactiveMetadataSchema,
);
const selector = 'script[enactive\\.site-id]';

const isDebug = new URLSearchParams(window.location.search).has(
  'enactiveDebug',
);

export const useEnactiveMetadata = (): EnactiveMetadata | undefined => {
  const href = useLocationHref();

  const qs = useLocationSearchParams();

  const [script, setScript] = React.useState<HTMLScriptElement>();
  const [dataString, setDataString] = React.useState<string>();

  const [localStorageContext, setLocalStorageContext] =
    React.useState<RecursivePartial<EventContext>>();
  const [scriptMetadata, setScriptMetadata] =
    React.useState<EnactiveMetadata>();
  const [metadata, setMetadata] = React.useState<EnactiveMetadata>();

  React.useEffect(() => {
    const s = document.querySelector<HTMLScriptElement>(selector);
    if (s === null) {
      console.warn('Could not locate enactive script on page', { selector });
    } else {
      setScript(s);
    }

    const context = (() => {
      try {
        return (
          (JSON.parse(
            localStorage.getItem('enactive-context') ?? 'null',
          ) as RecursivePartial<EventContext> | null) ?? undefined
        );
      } catch {
        return undefined;
      }
    })();
    if (context === undefined) {
      // Create a user id for local storage that will help track the user in a somewhat stable
      // way and remember tours that they have dismissed server-side
      const c: RecursivePartial<EventContext> = {
        user: {
          anonymous: true,
          id: uuidv4(),
        },
      };
      setLocalStorageContext(c);

      try {
        localStorage.setItem('enactive-context', JSON.stringify(c));
      } catch (err) {
        console.warn(
          'Failed to set enactive device data in local storage.',
          err,
        );
      }
    } else {
      setLocalStorageContext(context);
    }
  }, []);

  React.useEffect(() => {
    // Whenever the script is set, update our script data
    debug(
      'metadata update triggered from script reference or query string change',
      () => ({ qs, script }),
    );
    setDataString(encodeDataString(script, qs));
    if (script !== undefined) {
      // Whenever the script attributes changes, attach a mutation observer that updates the data
      // whenever the properties change. This way live context changes can be handled
      const observer = new MutationObserver(() => {
        debug('metadata update triggered from script property change');
        setDataString(encodeDataString(script, qs));
      });
      observer.observe(script, { attributes: true });
      return () => observer.disconnect();
    }
  }, [qs, script]);

  // Whenver the metadata string changes, decode it into script metadata
  React.useEffect(
    () => setScriptMetadata(decodeDataString(dataString, { selector })),
    [dataString],
  );

  React.useEffect(() => {
    const metadata =
      localStorageContext !== undefined && scriptMetadata !== undefined
        ? deepExtend<
            RecursivePartial<EnactiveMetadata>,
            RecursivePartial<EnactiveMetadata>,
            RecursivePartial<EnactiveMetadata>,
            EnactiveMetadata
          >(
            {},
            { context: { page: { url: href } } },
            { context: localStorageContext },
            scriptMetadata,
          )
        : undefined;
    setMetadata(metadata);
    debug('metadata updated', () => ({ metadata }));
  }, [href, scriptMetadata, localStorageContext]);

  return metadata;
};

function encodeDataString(
  script: HTMLScriptElement | undefined,
  qs: URLSearchParams,
): string | undefined {
  if (script !== undefined) {
    const params = new URLSearchParams();

    // First grab the metadata attributes from the script tag attributes
    debug('encoding script attributes', () => ({ script }));
    for (const attr of script.attributes) {
      if (attr.name.startsWith('enactive.') === true) {
        params.append(attr.name.substring(9), attr.value);
      }
    }

    // Override metadata attributes with query string parameters
    debug('encoding querystring attributes', () => ({ qs: qs.toString() }));
    for (const [key, value] of qs.entries()) {
      if (key.startsWith('enactive.') === true) {
        params.append(key.substring(9), value);
      }
    }

    // Assign defaults
    if (params.has('api-host') === false) {
      const a = document.createElement('a');
      a.href = script.src;
      params.append('api-host', `api.${a.hostname.replace('www.', '')}`);
    }
    if (params.has('enabled') === false) {
      params.append('enabled', 'true');
    }

    params.sort();

    debug('encoding final attributes', () => ({
      attributes: qs.toString(),
    }));

    return params.toString();
  } else {
    return undefined;
  }
}

function decodeDataString(
  data: string | undefined,
  { selector = '' } = {},
): EnactiveMetadata | undefined {
  if (data !== undefined) {
    const result = attributesToObject(new URLSearchParams(data).entries());
    debug('decoded raw metadata from attributes', () => ({ data, result }));

    if (compiled(result)) {
      // If a user id was set and anonymous was not explicitly set, we treat it as non-anonymous
      if (result.context?.user?.id !== undefined) {
        nullishAssignment(result.context.user, 'anonymous', false);
      }

      debug('decoded coerced metadata from attributes', () => ({
        data,
        result,
      }));

      return result;
    } else {
      console.warn('Invalid enactive script tag metadata', {
        errors: compiled.errors,
        result,
        selector,
      });
      return undefined;
    }
  } else {
    return undefined;
  }
}

function debug(message: string, data?: () => Record<string, unknown>): void {
  if (isDebug === true) {
    console.debug(`[enactive] ${message}`, data?.());
  }
}
